








































































































































































































.page-wrap {
  background-image: url(../assets/images/bg-inner.jpg);
  height: 100vh;

  @media (max-width: 767px) {
    height: auto;
    min-height: 100vh;
  }
}

.inner-logo {
  cursor: pointer;
}

.inner-page {
  padding: 10px 0;
}

.inner-header {
  position: relative;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-left: 70px;
  padding-right: 100px;
}

@media (max-width: 991px) {
  .logo-stat {
    max-width: 210px;
  }

  .inner-header {
    padding-left: 15px;
  }

  .map {
    height: calc(100vh - 76px);
  }
}

@media (max-width: 767px) {
  .inner-content {
    flex-direction: column;
    padding-top: 15px;
    padding-bottom: 80px;
  }

  .inner-header {
    padding-right: 15px;
  }

  .map {
    height: auto;
    margin: 0 15px;
  }

  .logo-stat {
    display: none;
  }

  .books {
    display: block;
  }
}

@media (max-width: 575px) {
  .map {
    display: none;
  }
}
